import styles from './Skills.module.scss';
import {Skill, Wrapper} from "Components";
import {HTML, JavaScript, React, Redux, SASS, TypeScript} from "common";
import {ReactFragment} from "react";

type SkillsType = {
  name: string
  description: string
  icon: ReactFragment
}

const skills: SkillsType[] = [
  {
    name: 'HTML',
    description: 'HyperText Markup Language           -  .',
    icon: <HTML/>
  },
  {
    name: 'SCSS',
    description: 'Syntactically Awesome Stylesheets     CSS, ' +
      '     CSS-      .',
    icon: <SASS/>
  },
  {
    name: 'TypeScript',
    description: ' ,     -, ' +
      '  JavaScript.',
    icon: <TypeScript/>
  },
  {
    name: 'JavaScript',
    description: ' M  .  -, ' +
      '   .',
    icon: <JavaScript/>
  },
  {
    name: 'React',
    description: 'JavaScript-        . ',
    icon: <React/>
  },
  {
    name: 'Redux',
    description: '   JavaScript    ,     .',
    icon: <Redux/>
  },

]

export const Skills = () => {

  const skillMapping = skills.map(({
                                     name,
                                     icon,
                                     description
                                   }, i) => {
    return <Skill
      key={i}
      name={name}
      icon={icon}
      description={description}
    />
  })
  return (
    <Wrapper id='skills' title={'Skills'}>
      <div  className={styles.skills}>
        {skillMapping}
      </div>
    </Wrapper>
  );
};



